import React, {createContext, useEffect, useState} from 'react';
import {useWallet} from 'use-wallet';
import {MarmotFarm} from './MarmotFarm';


export interface MarmotFarmContext {
  marmotfarm?: MarmotFarm;
}

export const Context = createContext<MarmotFarmContext>({marmotfarm: undefined});

export const MarmotFarmProvider: React.FC = ({children}) => {
  const {ethereum, account, chainId, status, error, connect} = useWallet();
  const [marmotfarm, setMarmotFarm] = useState<MarmotFarm>();

  useEffect(() => {
    const marmotfarm = new MarmotFarm();
    if (account) {
      // wallet was unlocked at initialization
      marmotfarm.unlockWallet(ethereum, account, chainId);
      console.log('account',account);
    }
    setMarmotFarm(marmotfarm);
    // console.log('account change', account);
  }, [account, chainId, status]);

  return <Context.Provider value={{marmotfarm}}> {children} </Context.Provider>;
};
