function padZero(value: number) {
    return value < 10 ? `0${value}` : value;
}

function getTimeLeft() {
    const timeEnd = new Date('2021/08/01 00:00:00'); // 结束时间
    let timeStart = new Date(); // 开始时间
    let timeLeft = timeEnd.getTime() - timeStart.getTime(); // 时间差的毫秒数

    //计算出相差天数
    const days = padZero(Math.floor(timeLeft / (24 * 3600 * 1000)));

    //计算出小时数
    const leave1 = timeLeft % (24 * 3600 * 1000); // 计算天数后剩余的毫秒数
    const hours = padZero(Math.floor(leave1 / (3600 * 1000)));

    //计算相差分钟数
    const leave2 = leave1 % (3600 * 1000); // 计算小时数后剩余的毫秒数
    const minutes = padZero(Math.floor(leave2 / (60 * 1000)));

    //计算相差秒数
    const leave3 = leave2 % (60 * 1000); // 计算分钟数后剩余的毫秒数
    const seconds = padZero(Math.round(leave3 / 1000));

    const daysArr = (days + '').split('');
    const hoursArr = (hours + '').split('');
    const minutesArr = (minutes + '').split('');
    const secondsArr = (seconds + '').split('');

    return {
        days,
        hours,
        minutes,
        seconds,
        timeLeft,
        daysArr,
        hoursArr,
        minutesArr,
        secondsArr,
    };
}

