import React, { useContext, useMemo } from 'react'
import styled, { ThemeContext } from 'styled-components'

import { Link } from 'react-router-dom'

interface ButtonProps {
  children?: React.ReactNode,
  disabled?: boolean,
  href?: string,
  onClick?: () => void,
  size?: 'xs' | 'sm' | 'md' | 'lg',
  text?: string,
  subText?: string,
  to?: string,
  variant?: 'default' | 'secondary' | 'tertiary',
  step: number,
}
interface ButtonWrapperProps {
  colors: string[]
}
interface MyDescriptionProps {
  mdcolor: string
}

const ButtonShine: React.FC<ButtonProps> = ({
  children,
  disabled,
  href,
  onClick,
  size,
  text,
  subText,
  to,
  variant,
  step,
}) => {
  const { color, spacing } = useContext(ThemeContext)

  let buttonColor: string
  switch (variant) {
    case 'secondary':
      buttonColor = color.white
      break
    case 'default':
    default:
      buttonColor = color.white
  }

  let boxShadow: string | undefined
  let buttonSize: number
  let buttonPadding: number
  let fontSize: number
  switch (size) {
    case 'xs':
      buttonPadding = 0
      buttonSize = 24
      fontSize = 12
      break
    case 'sm':
      buttonPadding = spacing[3]
      buttonSize = 36
      fontSize = 14
      break
    case 'lg':
      buttonPadding = spacing[4]
      buttonSize = 72
      fontSize = 16
      break
    case 'md':
    default:
      buttonPadding = spacing[4]
      buttonSize = 56
      fontSize = 16
  }

  const ButtonChild = useMemo(() => {
    if (to) {
      return <StyledLink to={to}>{text}</StyledLink>
    } else if (href) {
      return <StyledExternalLink href={href} target="__blank">{text}</StyledExternalLink>
    } else {
      return text
    }
  }, [href, text, to])

  // const MyDescriptionColors = ['#988c8c', '#9c9090', '#9c9090', '#988c8c'];
  const MyDescriptionColors = [color.white, color.white, color.white, color.white];
  const MyDescriptionColorIndex = step > 75 ? 3 : step > 50 ? 2 : step > 25 ? 1 : 0;
  const MyDescriptionColor = MyDescriptionColors[MyDescriptionColorIndex];


  const ButtonAscendingColors = [
    '#666666', '#AFDC2E', '#4DC26B', '#20908C', '#345F8D', '#482A7A'
  ]


  let colorsNow: any[] = [];
  let colorsUp: string;
  let colorsDown: string;
  if (step == 0) {
    colorsUp = '#666666';
    colorsDown = '#666666';
  } else {
    colorsUp = ButtonAscendingColors[parseInt((step / 20) + 1 + '')]
    colorsDown = ButtonAscendingColors[parseInt((step / 20) + '')]
  }
  // let colorsUp = ButtonAscendingColors[parseInt((step / 20) + '')];
  // let colorsDown = step == 0? '#666666' :
  colorsNow = [colorsUp, colorsDown]
  return (
    <ButtonWrapper colors={colorsNow}>
      <StyledButton
        boxShadow={boxShadow}
        color={buttonColor}
        disabled={disabled}
        fontSize={fontSize}
        onClick={onClick}
        padding={buttonPadding}
        size={buttonSize}
      >
        <LineOne>
          {children}
          {ButtonChild}
        </LineOne>
        <MyDescription mdcolor={MyDescriptionColor}>{subText}</MyDescription>
      </StyledButton>
    </ButtonWrapper>
  )
}


interface StyledButtonProps {
  boxShadow: string | undefined,
  color: string,
  disabled?: boolean,
  fontSize: number,
  padding: number,
  size: number
}


const ButtonWrapper = styled.div<ButtonWrapperProps>`
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  width: 300px;
  background-image: linear-gradient(${props => props.colors[0] }, ${props => props.colors[1] });
  box-shadow: 0 0 10px 2px ${props => props.colors[1]};
  border-radius: 30px;
`


const LineOne = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`

const MyDescription = styled.div<MyDescriptionProps>`
  padding: 4px 0 0 0;
  color: ${props => props.mdcolor };
  font-size: 12px;
`

const StyledButton = styled.button<StyledButtonProps>`
  background-color: transparent;
  border: 0;
  border-radius: 12px;
  box-shadow: ${props => props.boxShadow};
  color: ${props => !props.disabled ? props.color : `${props.color}55`};
  cursor: pointer;
  display: flex;
  flex-direction: column;
  font-size: ${props => props.fontSize}px;
  font-weight: 700;
  height: ${props => props.size}px;
  justify-content: center;
  outline: none;
  padding-left: ${props => props.padding}px;
  padding-right: ${props => props.padding}px;
  padding-top: 5px;
  padding-bottom: 5px;
  pointer-events: ${props => !props.disabled ? undefined : 'none'};
  width: 100%;
  position: relative;
  z-index: 2;
`

const StyledLink = styled(Link)`
  align-items: center;
  color: inherit;
  display: flex;
  flex: 1;
  height: auto;
  justify-content: center;
  margin: 0 ${props => -props.theme.spacing[4]}px;
  padding: 0 ${props => props.theme.spacing[4]}px;
  text-decoration: none;
`

const StyledExternalLink = styled.a`
  align-items: center;
  color: inherit;
  display: flex;
  flex: 1;
  height: auto;
  justify-content: center;
  margin: 0 ${props => -props.theme.spacing[4]}px;
  padding: 0 ${props => props.theme.spacing[4]}px;
  text-decoration: none;
`

export default ButtonShine