let useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});

interface Data {
  tableTitle: string[];
  tableData: [];
}

interface OrderTableProps {
  data: Data | any;
  withAction: boolean;
  withTitle?: boolean;
  minWidth?: number;
  title: string;
}

const OrderTable: React.FC<OrderTableProps> = (props) => {
  const title = props.title;
  const withAction = props.withAction;
  const withTitle = typeof props.withTitle === 'undefined' ? true : props.withTitle;
  const minWidth = props.minWidth;

  let headRows = props.data.tableTitle;
  let rows = props.data.tableData;
  // rows = rows.map((row: any[]) => createData1(row, headRows));

  if (minWidth) {
    useStyles = makeStyles({
      table: {
        minWidth: minWidth,
      },
      '@media (max-width: 768px)': {
        table: {
          minWidth: '200px',
        },
      },
    });
  }
  const classes = useStyles();

  return (
    <Padding>
      <TableContainer>
        <Table className={classes.table} aria-label='customized table'>
          {withTitle && (
            <TableHead>
              <TableRow>
                {headRows.map((row: any, index: number) =>
                  index === 0 ? (
                    <StyledTableCell key={`${title}-${row}-${index}`}>{row}</StyledTableCell>
                  ) : (
                    <StyledTableCell key={`${title}-${row}-${index}`} align='right'>{row}</StyledTableCell>
                  )
                )}
                {withAction && <StyledTableCell align='right'>Action</StyledTableCell>}
              </TableRow>
            </TableHead>
          )}
          {rows.length ? (
            <TableBody>
              {rows.map((row: any, index: number) => (
                <StyledTableRow key={`${title}-${row.id}`}>
                  {headRows.map((title: any, tindex: number) =>
                    tindex === 0 ? (
                      <StyledTableCell key={`${title}-${row.id}-${tindex}`} component='th' scope='row'>
                        {row[title]}
                      </StyledTableCell>
                    ) : (
                      <StyledTableCell key={`${title}-${row.id}-${tindex}`} align='right'>{row[title]}</StyledTableCell>
                    )
                  )}
                  {withAction && (
                    <StyledTableCell align='right'>
                      <StyledBalanceMax onClick={() => {}}>Market Close</StyledBalanceMax>
                    </StyledTableCell>
                  )}
                </StyledTableRow>
              ))}
            </TableBody>
          ) : (
            <TableBody>
              <TableRow>
                <StyledTableCell colSpan={9}>
                  <NoData>
                    <svg
                      width='64'
                      height='41'
                      viewBox='0 0 64 41'
                      xmlns='http://www.w3.org/2000/svg'
                    >
                      <g transform='translate(0 1)' fill='none' fill-rule='evenodd'>
                        <ellipse cx='32' cy='33' rx='32' ry='7'></ellipse>
                        <g fill-rule='nonzero' stroke='#9e9e9e'>
                          <path d='M55 12.76L44.854 1.258C44.367.474 43.656 0 42.907 0H21.093c-.749 0-1.46.474-1.947 1.257L9 12.761V22h46v-9.24z'></path>
                          <path
                            d='M41.613 15.931c0-1.605.994-2.93 2.227-2.931H55v18.137C55 33.26 53.68 35 52.05 35h-40.1C10.32 35 9 33.259 9 31.137V13h11.16c1.233 0 2.227 1.323 2.227 2.928v.022c0 1.605 1.005 2.901 2.237 2.901h14.752c1.232 0 2.237-1.308 2.237-2.913v-.007z'
                            fill='#9e9e9e'
                          ></path>
                        </g>
                      </g>
                    </svg>
                    <NoDataText>No Data</NoDataText>
                  </NoData>
                </StyledTableCell>
              </TableRow>
            </TableBody>
          )}
        </Table>
      </TableContainer>
    </Padding>
  );
};

