import {BigNumber, Contract, ethers} from 'ethers';
import {TERC20, MarmotPool, MarmotToken, SymbolOracleWoo} from './ABI.json';
import {BN2decimal, BN2display, decimal2BN, decimal2display, decimalDiv, decimalMul, ONE} from "../utils/Converter";
import {web3ProviderFrom} from '../para/ether-utils';
import config from '../config';

export function getDefaultProvider(): ethers.providers.Web3Provider {
    return new ethers.providers.Web3Provider(
        web3ProviderFrom(config.defaultProvider),
        config.chainId,
    )
}

const MAX_INT = ethers.constants.MaxUint256;
const ClaimTestBUSDAmount = decimal2BN("10000")


let ContractAddress = {
    BUSD: "0x386439cf77D1d71EE95f6ca17eA2ee5e9268c324",
    BTC: "0x98FAD7c788b30423815F2Eb7ce83Df827F3E0949",
    ETH: "0x2B3a4D77C84E1389D5A47E2Db53eb968C8F1A503",
    BNB: "0xAf15168B65c77fd4813FfE2a23a406F17eaf05b8",
    MARMOT: "0xA9783F9407923b66719F96a02A853883e78BD0F9",
    POOL: "0xC9A8e9960ce7B3B98F32D1B9Ee4348eEE3801c52",
    oracleBTCUSD: "0x78Db6d02EE87260a5D825B31616B5C29f927E430",
    oracleETHUSD: "0xdF0050D6A07C19C6F6505d3e66B68c29F41edA09",
}

/**
 * An API module of Basis Cash contracts.
 * All contract-interacting domain logic should be defined in here.
 */
declare global {
    interface Window {
        ethereum: any;
    }
}

export class MarmotFarm {
    myAccount: string | undefined;
    provider: ethers.providers.Web3Provider;
    signer?: ethers.Signer;
    contracts: { [name: string]: Contract };

    constructor() {
        const provider = getDefaultProvider();
        this.provider = provider;
        this.contracts = {};
        this.contracts["pool"] = new Contract(ContractAddress.POOL, MarmotPool, this.provider);
        this.contracts["marmot"] = new Contract(ContractAddress.MARMOT, MarmotToken, this.provider);
        this.contracts['busd'] = new Contract(ContractAddress.BUSD, TERC20, this.provider);
        this.contracts['btc'] = new Contract(ContractAddress.BTC, TERC20, this.provider);
        this.contracts['eth'] = new Contract(ContractAddress.ETH, TERC20, this.provider);
        this.contracts['bnb'] = new Contract(ContractAddress.BNB, TERC20, this.provider);
        this.contracts['btc_oracle'] = new Contract(ContractAddress.oracleBTCUSD, SymbolOracleWoo, this.provider);
        this.contracts['eth_oracle'] = new Contract(ContractAddress.oracleETHUSD, SymbolOracleWoo, this.provider);

    }

    /**
     * @param provider From an unlocked wallet. (e.g. Metamask)
     * @param account An address of unlocked wallet account.
     */
    unlockWallet(provider: any, account: string, chainId: any) {
        const newProvider = new ethers.providers.Web3Provider(provider, chainId);
        this.signer = newProvider.getSigner();
        this.myAccount = account;
        this.contracts["pool"] = this.contracts["pool"].connect(this.signer);
        console.log(`🔓 Wallet is unlocked. Welcome, ${account}!`);
    }


    get isUnlocked(): boolean {
        return !!this.myAccount;
    }


    async mint(symbol:string): Promise<any> {
        await this.provider.ready;
        let amount: BigNumber
        if (symbol == 'busd') amount = decimal2BN("10000")
        if (symbol == 'btc') amount = decimal2BN("1")
        if (symbol == 'eth') amount = decimal2BN("10")
        if (symbol == 'bnb') amount = decimal2BN("10")
        return await this.contracts[symbol].connect(this.signer).mint(this.myAccount, amount)
    }

    async mintAll(): Promise<any> {
        await this.provider.ready;
        await this.mint('busd')
        await this.mint('btc')
        await this.mint('eth')
        // await this.mint('bnb')
        return this.mint('bnb')
    }

    async approve(symbol:string): Promise<any> {
        return await this.contracts[symbol].connect(this.signer).approve(ContractAddress.POOL, MAX_INT)
    }


    async deposit(symbol: string, amount: BigNumber): Promise<any> {
        let pid: number
        if (symbol == 'busd') pid = 0
        if (symbol == 'btc') pid = 1
        if (symbol == 'eth') pid = 2
        if (symbol == 'bnb') pid = 3
        return await this.contracts['pool'].connect(this.signer).deposit(pid, amount)
    }

    async withdraw(symbol: string, amount: BigNumber): Promise<any> {
        let pid: number
        if (symbol == 'busd') pid = 0
        if (symbol == 'btc') pid = 1
        if (symbol == 'eth') pid = 2
        if (symbol == 'bnb') pid = 3
        return await this.contracts['pool'].connect(this.signer).withdraw(pid, amount)
    }

    async claimAll(): Promise<any> {
        return await this.contracts['pool'].connect(this.signer).claimAll()
    }

    async pendingAll(): Promise<any> {
        return await this.contracts['pool'].connect(this.signer).pendingAll()
    }

    async pending(symbol: string): Promise<any> {
        let user: string = await this.signer.getAddress()
        let pid: number
        if (symbol == 'busd') pid = 0
        if (symbol == 'btc') pid = 1
        if (symbol == 'eth') pid = 2
        if (symbol == 'bnb') pid = 3
        return await this.contracts['pool'].pending(pid, user)
    }

    async getPrice(symbol: string): Promise<any> {
        if (symbol == 'btc') return await this.contracts['btc_oracle'].getPrice()
        if (symbol == 'eth') return await this.contracts['eth_oracle'].getPrice()
    }

    async getUserTotal(): Promise<any> {
        const reward = BN2decimal(await this.contracts['pool'].connect(this.myAccount).pendingAll())
        return reward
    }

    async getUserInfo(symbol: string): Promise<any> {
        let pid: number
        if (symbol == 'busd') pid = 0
        if (symbol == 'btc') pid = 1
        if (symbol == 'eth') pid = 2
        if (symbol == 'bnb') pid = 3
        const balance = BN2decimal(await this.contracts[symbol].balanceOf(this.myAccount));
        const userInfo = await this.contracts['pool'].getUserInfo(pid, this.myAccount);
        const deposit = BN2decimal(userInfo.amount)
        const rewardDebt = BN2decimal(userInfo.rewardDebt)
        const allowance = BN2decimal(await this.contracts[symbol].allowance(this.myAccount, ContractAddress.POOL))
        const pendingReward = BN2decimal(await this.contracts['pool'].pending(pid, this.myAccount))
    return {balance, deposit, rewardDebt, allowance, pendingReward}
    }

    async getPoolInfo(symbol: string): Promise<any> {
        let pid: number
        if (symbol == 'busd') pid = 0
        if (symbol == 'btc') pid = 1
        if (symbol == 'eth') pid = 2
        if (symbol == 'bnb') pid = 3

        const poolInfo = await this.contracts['pool'].getPoolInfo(pid);

        const discount = BN2decimal(poolInfo.discount);
        const multiplier = parseFloat(BN2decimal(poolInfo.discount)) / 0.1;
        const amount = BN2decimal(poolInfo.totalShare);
        console.log("poolInfo", pid, discount, amount)
        let value
        if (pid > 0) {
            const price = await this.contracts['pool'].getPrice(pid)
            value = BN2decimal(decimalMul(poolInfo.totalShare, price))
        }
        else {
            value = amount
        }
    return {discount, amount, value, multiplier}
    }
}
