import React, { useState, useEffect, useCallback } from 'react';
import styled from 'styled-components';
import { createChart, IChartApi } from 'lightweight-charts';

interface TradingviewProps {
  data: any;
  changeCycle: (i: any) => {};
}


const Tradingview: React.FC<TradingviewProps> = (props) => {
  // console.log(props.data);
  const [initChart, setInitChart] = useState(false);
  const [dateIndex, setDateIndex] = useState(0);

  let areaSeries: any;

  const handleLoadLightweightCharts = useCallback(() => {
    if (initChart) return;
    setInitChart(true);

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const chart = createChart('lightweightCharts', {
      width: 680,
      height: 500,
      layout: {
        backgroundColor: '#212429',
        textColor: '#fff', // #696969
        fontSize: 12,
        fontFamily: 'Calibri',
      },
      grid: {
        vertLines: {
          // color: 'rgba(70, 130, 180, 0.1)',
          // style: 1,
          visible: false,
        },
        horzLines: {
          // color: 'rgba(70, 130, 180, 0.1)',
          // style: 1,
          visible: false,
        },
      },
      crosshair: {
        vertLine: {
          color: '#6A5ACD',
          style: 1,
          visible: true,
          labelVisible: true,
        },
        horzLine: {
          color: '#6A5ACD',
          style: 0,
          visible: true,
          labelVisible: true,
        },
        mode: 1,
      },
    });

    // eslint-disable-next-line react-hooks/exhaustive-deps
    areaSeries = chart.addAreaSeries({
      topColor: 'rgba(21, 146, 230, 0.4)',
      bottomColor: 'rgba(21, 146, 230, 0)',
      lineColor: 'rgba(21, 146, 230, 1)',
      lineStyle: 0,
      lineWidth: 2,
      crosshairMarkerVisible: false,
      crosshairMarkerRadius: 2,
      crosshairMarkerBorderColor: 'rgb(255, 255, 255, 1)',
      crosshairMarkerBackgroundColor: 'rgb(34, 150, 243, 1)',
    });
    areaSeries.setData(props.data);
  }, [initChart]);
  
  // const updateChart = useCallback(() => {
  //   console.log(props.data);
    
  //   areaSeries && areaSeries.setData(props.data);
  // }, [areaSeries, props.data])

  useEffect(() => {
    handleLoadLightweightCharts();
  }, [handleLoadLightweightCharts]);

  // useEffect(() => {;
  //   updateChart();
  // }, [updateChart]);

  const handleNavChange = (ev: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    if (!(ev.target instanceof HTMLSpanElement)) {
      return;
    }
    const i = parseInt(ev.target.dataset.dateIndex);
    setDateIndex(i);
    props.changeCycle(i);
  }
  return (
    <StyledTradingview>
      {/* {children} */}
      <div>
        <StyledSelectTitme onClick={(e)=> handleNavChange(e)}>
          <StyledSpan active={dateIndex === 0} data-date-index={0}>24H</StyledSpan>
          <StyledSpan active={dateIndex === 1} data-date-index={1}>1W</StyledSpan>
          <StyledSpan active={dateIndex === 2} data-date-index={2}>1M</StyledSpan>
        </StyledSelectTitme>
        <div id='lightweightCharts'>
        </div>
      </div>
    </StyledTradingview>
  );
};

const StyledTradingview = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: ${(props) => props.theme.spacing[4]}px;
`;
const StyledSelectTitme = styled.div`
  display: flex;
  flex: 1;
`;
const StyledSpan1 = styled.div`
  margin-right: 10px;
  cursor: pointer;
`;
const StyledSpan = styled(({ active, ...otherProps }) => <span {...otherProps} />)`
  margin-right: 10px;
  cursor: pointer;
  color: ${({ active }) => active ? '#9090FF' : '#9e9e9e'}
`
const StyledWidgetContainer = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: ${(props) => props.theme.spacing[4]}px;
`;

export default Tradingview;
