const uriList: {[index: string]:any} = {
  'busd': 'https://exchange.pancakeswap.finance/images/coins/0xe9e7cea3dedca5984780bafc599bd69add087d56.png',
  'btc': 'https://exchange.pancakeswap.finance/images/coins/0x7130d2a12b9bcbfae4f2634d864a1ee1ce3ead9c.png',
  'eth': 'https://exchange.pancakeswap.finance/images/coins/0x2170ed0880ac9a755fd29b2688956bd959f933f8.png',
  'bnb': 'https://raw.githubusercontent.com/trustwallet/assets/master/blockchains/smartchain/assets/0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c/logo.png',
  'marmot': MARMOT_LOGO,
}

const CurrencyLogo: React.FC<any> = ({name, size='24px', symbol2, style }) => {
  return (
      <>
      {symbol2 ?
        (
          <AvatarGroup>
            <Avatar alt="" src={uriList[name]} style={{height: size, width: size}}/>
            <Avatar alt="" src={uriList[symbol2]} style={{marginLeft: '-25px', height: size, width: size}}/>
          </AvatarGroup>
        )
        : (
          <AvatarGroup>
            <Avatar alt="" src={uriList[name]} style={{height: size, width: size}}/>
          </AvatarGroup>
        )
      }
    </>
  )
}

const AvatarGroup = styled.div`
  display: flex;
  position: relative;
  padding-right: 5px;
`;

export default CurrencyLogo;