let useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});

interface Data {
  tableTitle: string[];
  tableData: [];
}

interface OtherTableProps {
  data: Data | any;
  withAction: boolean;
  withTitle?: boolean;
  minWidth?: number;
}

const OtherTable: React.FC<OtherTableProps> = (props) => {
  const withAction = props.withAction;
  const withTitle = typeof props.withTitle === 'undefined' ? true : props.withTitle;
  const minWidth = props.minWidth;
  
  let headRows = props.data.tableTitle;
  let rows = props.data.tableData;
  // rows = rows.map((row: any[]) => createData1(row, headRows));

  if (minWidth) {
    useStyles = makeStyles({
      table: {
        minWidth: minWidth,
      },
    });
  }
  const classes = useStyles();

  return (
    <Padding>
      <TableContainer>
        <Table className={classes.table} aria-label='customized table'>
          {
            withTitle &&  <TableHead>
              <TableRow>
                {
                  headRows.map((row: any, index: number) => (
                    index === 0 ? <StyledTableCell>{row}</StyledTableCell> : <StyledTableCell align='right'>{row}</StyledTableCell>
                  ))
                }
                {
                  withAction && <StyledTableCell align='right'>Action</StyledTableCell>
                }
              </TableRow>
            </TableHead>
          }
          <TableBody>
            {rows.map((row: any, index: number) => (
              <StyledTableRow key={row.id}>
                {
                  headRows.map((title: any, tindex: number) => (
                    tindex === 0 ?
                      <StyledTableCell component='th' scope='row'>{row[title]}</StyledTableCell> :
                      <StyledTableCell align='right'>{row[title]}</StyledTableCell>
                  ))
                }
                {
                  withAction && <StyledTableCell align='right'>
                    <StyledBalanceMax onClick={() => {}}>Market Close</StyledBalanceMax>
                  </StyledTableCell>
                }
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Padding>
  );
};

